import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';

import HelpOutlineIcon from '@material-ui/icons/HelpOutline';

const useStyles = makeStyles((theme) => ({
  root: {},
  explainable: {
    // color: "#556cd6",
    borderBottom: "1px dashed",
    textDecoration:"none",
    cursor: "pointer",
    "& sup": {
      fontSize: "0.875rem",
      marginRight: "4px",
      lineHeight: "0"
    }
  }
}))

export default function ExplainString({children,sameWindow, ...prop}) {
  const classes = useStyles()
  const {href, title} = prop

  function handelClick (e){
    if (Boolean(prop.href) && !Boolean(sameWindow)){
    e.preventDefault()
    window.open(prop.href,'_blank')
    }
  };

  return (
    <a
      className={clsx({[classes.explainable]:(Boolean(href) || Boolean(title))})}
      onClick={handelClick}
      {...prop}
    >
    {children}
    {/* <sup>!</sup> */}
    </a>
  )
}
